:root {
  --primary-clr: rgba(9, 94, 147, 1);
  --light-primary-clr: rgba(9, 94, 147, .7);
  --dark-primary-clr: rgba(5, 10, 39, 1);
  --light-dark-primary-clr: rgba(5, 10, 39, .65);
  --secondary-clr: goldenrod;
  --text-clr-primary: #ffffff;
}

/* Main */
html, body {
  height: 100vh;
  margin: 0 auto;
}

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

/* Header and Nav Section */
.App-header {
  background-image: url("../../assets/images/technology-background.jpg");
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  height: 100%;
  font-size: calc(10px + 2vmin);
  color: var(--text-clr-primary);
}

.h1 {
  margin-top: 0;
  padding-top: 2rem;
}

.App-link {
  color: var(--text-clr-primary);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  list-style: none;
  padding: 0;
}

.nav-link {
  text-decoration: none;
  color: var(--text-clr-primary);
}

.active {
  color: var(--secondary-clr);
  background-color: var(--primary-clr);
  padding: 1rem;
}

.list-links {
  width: 100%;
  padding-bottom: 1rem;
}

.nav-link:hover {
  color: var(--secondary-clr);
}

/* **Body Section** */
.App {
  text-align: center;
}

.App-body {
  background-color: var(--primary-clr);
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
}

.title, .aboutMe {
  margin: 0 auto;
  padding: 2rem 0 2rem 0;
  color: var(--text-clr-primary);
  width: 60%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.wrapper {
  background-color: var(--primary-clr);
  padding: 0;
  text-align: center;
  overflow-y: auto;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}

ul {
  margin-bottom: 0;
  padding-bottom: 1rem;
}

/* About Section */
.aboutMe {
  padding: 2.5rem 0;
  font-size: 1.125rem;
}

.about-image {
  width: 80%;
  border-radius: 40px;
}

@media screen and (max-width: 768px) {
  .aboutMe {
    padding: 1rem;
    font-size: 1rem;
    margin: 0 1rem;
    width: 80%;
  }
}

@media screen and (min-width: 768px) {
  .about-image {
    width: 40%;
  }
}

/* Portfolio Section*/
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
}

.column {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  -webkit-box-flex: 33%;
      -ms-flex: 33%;
          flex: 33%;
}

.card {
  -webkit-transition: all .35s;
  transition: all .35s;
  position: relative;
  border-radius: 15px;
}

.card:hover {
  background-image: url("../../assets/images/technology-background.jpg");
}

.project-image {
  width: 95%;
  padding: 1rem 0;
}

.image-container:hover + .content {
  display: block;
}

.content:hover {
  display: block;
}

.card-title {
  color: var(--secondary-clr);
  background-image: url("../../assets/images/technology-background.jpg");
  border-radius: 15px;
  padding: .5rem;
}

.content a {
  text-decoration: none;
}

.card-title:hover {
  font-size: 2rem;
  -webkit-transition: all .35s;
  transition: all .35s;
}

.gitIcon {
  background-color: var(--primary-clr);
  width: 3.5rem;
  -webkit-transition: all .35s;
  transition: all .35s;
  border-radius: 15px;
}

.gitIcon:hover {
  width: 4.25rem;
}

@media screen and (min-width: 768px) {
  .content {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: absolute;
    display: none;
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 768px) {
  .column {
    -webkit-box-flex: 100%;
        -ms-flex: 100%;
            flex: 100%;
  }
  .card {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  .content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  .gitIcon {
    width: 2rem;
  }
}

@media screen and (min-width: 768px) {
  .column {
    -webkit-box-flex: 50%;
        -ms-flex: 50%;
            flex: 50%;
  }
}

@media screen and (min-width: 1200px) {
  .column {
    -webkit-box-flex: 33%;
        -ms-flex: 33%;
            flex: 33%;
  }
}

/* Contact Section */
#contact-form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 0 auto;
  width: 60%;
  padding: 0 2rem;
  background-image: url("../../assets/images/technology-background.jpg");
  border-radius: 15px;
}

#contact-form div {
  width: 50%;
}

label {
  color: var(--text-clr-primary);
  font-size: 1.25rem;
  margin: 1rem;
}

input {
  width: 50%;
  height: 2rem;
  border-radius: 15px;
}

textarea {
  width: 100%;
  height: 10rem;
  border-radius: 15px;
  margin-top: 1.5rem;
}

button {
  width: 20%;
  height: 3rem;
  margin: 3rem 0 8rem 0;
  border-radius: 15px;
  background-color: var(--dark-primary-clr);
  color: var(--text-clr-primary);
  font-size: 1.5rem;
}

button:hover {
  color: var(--secondary-clr);
}

.error-text {
  color: var(--secondary-clr);
  background-color: var(--light-dark-primary-clr);
  font-size: 1.5rem;
  width: 50%;
  margin: 0 auto;
}

@media screen and (max-width: 768px) {
  button {
    width: 50%;
  }
  input {
    width: 80%;
  }
}

/* Resume Section */
.skills {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0 auto;
  width: 30%;
  padding: 1rem;
  color: var(--text-clr-primary);
  height: 50vh;
}

.skills p {
  font-size: 1.5rem;
}

.frontEndSkills, .backEndSkills {
  padding: 1rem;
  background-image: url("../../assets/images/technology-background.jpg");
  border-radius: 15px;
}

.list {
  list-style: none;
  padding: 0;
}

.resumeDownload {
  font-size: 1.75rem;
  margin: 0;
  padding: 2rem 0 2rem 0;
  color: var(--text-clr-primary);
}

.resumeDownload > a {
  text-decoration: none;
  color: var(--text-clr-primary);
}

.resumeDownload > a:hover {
  color: var(--secondary-clr);
}

@media screen and (max-width: 1200px) {
  .skills {
    width: 50%;
  }
  .wrapper {
    height: 100vh;
  }
}

@media screen and (min-width: 1201px) {
  .skills {
    width: 50%;
  }
}

@media screen and (max-width: 768px) {
  .skills {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

/* Footer Section */
.App-footer {
  background-image: url("../../assets/images/technology-background.jpg");
  color: var(--text-clr-primary);
  height: 4rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.icons {
  background-color: var(--primary-clr);
  margin: 0 2rem;
  padding: 1.125rem;
}

.icons:hover {
  background-color: var(--secondary-clr);
}
